// js/settlementManager.js
class SettlementManager {
    constructor(gridResolution, heightMap, perlinNoise) {
        this.gridResolution = gridResolution;
        this.heightMap = heightMap;
        this.perlinNoise = perlinNoise;
        this.settlements = [];
        this.MICRO_MAP_GRID_SIZE = 32; // Defined here as it's specific to settlement layout
    }

    generateSettlements() {
        this.settlements = [];
        const numSettlements = 5;
        const minSettlementSize = 3;
        const maxSettlementSize = 6;

        for (let i = 0; i < numSettlements; i++) {
            const size = Math.floor(Math.random() * (maxSettlementSize - minSettlementSize + 1)) + minSettlementSize;
            const newSettlement = {
                x: Math.floor(Math.random() * (this.gridResolution - size)),
                y: Math.floor(Math.random() * (this.gridResolution - size)),
                size,
                name: `Barrio ${String.fromCharCode(65 + i)}`,
                microBuildingsLayout: Array(this.MICRO_MAP_GRID_SIZE).fill(0).map(() => Array(this.MICRO_MAP_GRID_SIZE).fill({ type: 'empty', heightOffset: 0 })),
                totalBuildings: 0,
                riskIcon: '' // Placeholder for dynamic update by renderer
            };

            const initialRoadSeeds = 3;
            const roadExpansionChance = 0.6;
            const minTerrainForBuilding = 0.2;
            const minTerrainForRoad = 0.1;
            const houseDensity = 0.6;

            const tempRoadGrid = Array(this.MICRO_MAP_GRID_SIZE).fill(0).map(() => Array(this.MICRO_MAP_GRID_SIZE).fill(false));
            const roadQueue = [];

            // Initial road seeds
            for (let k = 0; k < initialRoadSeeds; k++) {
                const randX = Math.floor(Math.random() * this.MICRO_MAP_GRID_SIZE);
                const randY = Math.floor(Math.random() * this.MICRO_MAP_GRID_SIZE);
                if (!tempRoadGrid[randY][randX]) {
                    tempRoadGrid[randY][randX] = true;
                    roadQueue.push({ x: randX, y: randY });
                }
            }

            // Road expansion (simple BFS-like expansion)
            let head = 0;
            while (head < roadQueue.length && roadQueue.length < this.MICRO_MAP_GRID_SIZE * this.MICRO_MAP_GRID_SIZE * 0.3) { // Limit road density
                const currentCell = roadQueue[head++];
                const neighbors = [{ dx: 0, dy: -1 }, { dx: 0, dy: 1 }, { dx: 1, dy: 0 }, { dx: -1, dy: 0 }];
                // Occasionally add diagonal neighbors for more organic roads
                if (Math.random() < 0.5) neighbors.push({ dx: 1, dy: 1 }, { dx: -1, dy: -1 }, { dx: 1, dy: -1 }, { dx: -1, dy: 1 });
                neighbors.sort(() => Math.random() - 0.5); // Randomize neighbor check order

                neighbors.forEach(n => {
                    const nx = currentCell.x + n.dx;
                    const ny = currentCell.y + n.dy;
                    if (nx >= 0 && nx < this.MICRO_MAP_GRID_SIZE && ny >= 0 && ny < this.MICRO_MAP_GRID_SIZE && !tempRoadGrid[ny][nx]) {
                        // Map micro-grid coordinate to main grid for terrain height check
                        const gridX_main = newSettlement.x + Math.floor(nx * newSettlement.size / this.MICRO_MAP_GRID_SIZE);
                        const gridY_main = newSettlement.y + Math.floor(ny * newSettlement.size / this.MICRO_MAP_GRID_SIZE);
                        let currentTerrainHeight = 0;
                        if (gridX_main >= 0 && gridX_main < this.gridResolution && gridY_main >= 0 && gridY_main < this.gridResolution) {
                            currentTerrainHeight = this.heightMap[gridY_main * this.gridResolution + gridX_main];
                        }
                        if (Math.random() < roadExpansionChance && currentTerrainHeight > minTerrainForRoad) {
                            tempRoadGrid[ny][nx] = true;
                            roadQueue.push({ x: nx, y: ny });
                        }
                    }
                });
            }

            // Populate micro-buildings layout
            for (let sy = 0; sy < this.MICRO_MAP_GRID_SIZE; sy++) {
                for (let sx = 0; sx < this.MICRO_MAP_GRID_SIZE; sx++) {
                    const gridX = newSettlement.x + Math.floor(sx * newSettlement.size / this.MICRO_MAP_GRID_SIZE);
                    const gridY = newSettlement.y + Math.floor(sy * newSettlement.size / this.MICRO_MAP_GRID_SIZE);
                    let terrainHeight = 0;
                    if (gridX >= 0 && gridX < this.gridResolution && gridY >= 0 && gridY < this.gridResolution) {
                        terrainHeight = this.heightMap[gridY * this.gridResolution + gridX];
                    }

                    if (tempRoadGrid[sy][sx]) {
                        newSettlement.microBuildingsLayout[sy][sx] = { type: 'road', heightOffset: -0.05 - Math.random() * 0.03 };
                    } else if (terrainHeight > minTerrainForBuilding && Math.random() < houseDensity) {
                        // Random house size for visual variety
                        newSettlement.microBuildingsLayout[sy][sx] = { type: 'house', size: Math.floor(Math.random() * 3) + 1, heightOffset: 0.15 + Math.random() * 0.1 };
                        newSettlement.totalBuildings++;
                    } else {
                        newSettlement.microBuildingsLayout[sy][sx] = { type: 'empty', heightOffset: 0 };
                    }
                }
            }
            this.settlements.push(newSettlement);
        }
    }

    getSettlements() {
        return this.settlements;
    }
}

export { SettlementManager };